.App {
	overflow-x: hidden;
	overflow-y: auto;
	height: 100vh;
	width: 100%;
	font-family: 'Roboto';
	background-color: #f5f3f2;
	color: #474544;
}
::selection {
	background-color: #dcf5f0;
	color: #3d7a6e;
}

input {
	color: #3d7a6e;
	background-color: #dcf5f0;
	font-family: 'Poppins';
	outline: #3d7a6e;
}

textarea {
	color: #3d7a6e;
	background-color: #dcf5f0;
	font-family: 'Poppins';
	border: 2px solid #71aca0;
	border-radius: 10px;
	outline: #3d7a6e;
	resize: none;
}

input::selection {
	color: #dcf5f0;
	background: #81b7ac;
}

textarea::selection {
	color: #dcf5f0;
	background: #81b7ac;
}

:root {
	overflow-x: hidden;
	overflow-y: auto;
}

main {
	margin: 2% 4%;
	height: 100vh;
}

h1 {
	font-family: Poppins;
	font-size: 32pt;
	font-weight: lighter;
}

.Header {
	display: block;
	width: 100%;
	background-color: #71aca0;
}

/* top half */
.headbar {
	display: block;
	width: 100%;
	background-color: #71aca0;
	padding-left: 40px;
}

.headbar img {
	max-height: 50px;
	padding: 5px 0;
}

/* bottom half */
nav {
	width: 100%;
	height: 40px;
	display: -webkit-inline-flex;
	display: inline-flex;
	-webkit-flex-flow: row nowrap;
	        flex-flow: row nowrap;
	-webkit-justify-content: space-evenly;
	        justify-content: space-evenly;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-align-content: center;
	        align-content: center;
	background-color: #81b7ac;
	padding: 0px 5px;
}

/* links */
nav a:link {
	-webkit-flex-grow: 1;
	        flex-grow: 1;
	color: #474544;
	font-family: Roboto;
	font-weight: medium;
	font-size: 18pt;
	text-decoration: none;
	text-align: center;
	padding: 5px;
}
/* visited */
nav a:visited {
	color: #474544;
}
/* hover/active */
nav a:hover,
nav a:active {
	background-color: #b1d8d0;
	transition: background 400ms ease-in-out;
}

/* searchbar */
form {
	-webkit-flex-grow: 2;
	        flex-grow: 2;
	padding: 0 3%;
}
input {
	white-space: nowrap;
	padding: 0.4em 1em;
	width: 100%;
	background-color: #dcf5f0;
	border: 2px solid #71aca0;
	border-radius: 30px;
	overflow: hidden;
	color: #3d7a6e;
}
/* .search-icon {
	position: absolute;
	top: 6px;
	left: 8px;
	width: 14px;
} */

* {
	margin: 0;
	padding: 0;
}
.stars {
	float: left;
	height: 46px;
	padding: 0 10px;
}
.stars:not(:checked) > input {
	position: absolute;
	top: -9999px;
}
.stars:not(:checked) > label {
	float: right;
	width: 1em;
	overflow: hidden;
	white-space: nowrap;
	cursor: pointer;
	font-size: 30px;
	color: #8b837f;
}
.stars:not(:checked) > label:before {
	content: '★ ';
}
.stars > input:checked ~ label {
	color: #474544;
}
.stars:not(:checked) > label:hover,
.stars:not(:checked) > label:hover ~ label {
	color: #474544;
}
.stars > input:checked + label:hover,
.stars > input:checked + label:hover ~ label,
.stars > input:checked ~ label:hover,
.stars > input:checked ~ label:hover ~ label,
.stars > label:hover ~ input:checked ~ label {
	color: #474544;
}

/* ==== ^ RATINGS ^ ==== */

/* === */

/* whole page  */
.BookDetail {
	width: 100%;
	height: 100%;
}

/* === */

/* vv START OF GRID  vv */
section {
	padding: 2%;

	display: grid;

	grid-template-columns: 1fr 1fr;
	grid-template-rows: auto auto;

	grid-column-gap: 5%;

	-webkit-column-gap: 5%;

	        column-gap: 5%;
	grid-row-gap: 8%;
	row-gap: 8%;
}

/* == LEFT == */

/* details of book */
.book-detail aside {
	grid-column: 1;
	grid-row: 1 / 2;
}

/* book title */
.book-detail h1 {
}
/* author */
.book-detail h3 {
	font-family: Poppins;
}

/* cover image */
.book-detail img {
}

/* desc */
.book-detail p {
}

/* buy link */
.book-detail a {
}

/* == RIGHT == */

/* review form (stars) (text) */
container {
	width: 100%;
	background-color: #e6e2e0;
	padding: 4%;
	border-radius: 15px;

	grid-column: 2;
	grid-row: 1 / 2;

	display: grid;
	-webkit-align-items: bottom;
	        align-items: bottom;
	justify-items: start;

	grid-template-columns: 2fr 1fr;
	grid-column-gap: 10px;
	-webkit-column-gap: 10px;
	        column-gap: 10px;

	grid-template-rows: 45px 15px 1fr;
	grid-row-gap: 5px;
	row-gap: 5px;
}

/* avg rating */
.Average {
	grid-column: 2;
	grid-row: 1;
	padding: 1%;
	/* align-self: center; */
}

/* 'leave a review' */
.leave-review h2 {
	font-family: Poppins;
	grid-column: 1;
	padding: 1%;
}

/* .leave-review p {
	grid-column: 1;
} */

.leave-review form {
	grid-column: 1 / span 2;
	width: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-flow: column nowrap;
	        flex-flow: column nowrap;
}

.stars {
	display: block;
	-webkit-align-self: flex-start;
	        align-self: flex-start;
}

.leave-review textarea {
	padding: 2%;
	width: 85%;
	height: 85%;
}

.leave-review button {
	padding: 1%;
	margin: 2%;
}

/* === */

/* list of past reviews */
.pastReviews {
	grid-column: 1 / 2;
	grid-row: 2;
}

/* 'reviews' */
h4 {
	font-family: Poppins;
}

/* ^^ END OF GRID  ^^ */

