@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600&family=Roboto:wght@300;400;500;700&display=swap');

.App {
	overflow-x: hidden;
	overflow-y: auto;
	height: 100vh;
	width: 100%;
	font-family: 'Roboto';
	background-color: #f5f3f2;
	color: #474544;
}

::-moz-selection {
	background-color: #dcf5f0;
	color: #3d7a6e;
}
::selection {
	background-color: #dcf5f0;
	color: #3d7a6e;
}

input {
	color: #3d7a6e;
	background-color: #dcf5f0;
	font-family: 'Poppins';
	outline: #3d7a6e;
}

textarea {
	color: #3d7a6e;
	background-color: #dcf5f0;
	font-family: 'Poppins';
	border: 2px solid #71aca0;
	border-radius: 10px;
	outline: #3d7a6e;
	resize: none;
}

input::selection {
	color: #dcf5f0;
	background: #81b7ac;
}

textarea::selection {
	color: #dcf5f0;
	background: #81b7ac;
}

:root {
	overflow-x: hidden;
	overflow-y: auto;
}

main {
	margin: 2% 4%;
	height: 100vh;
}

h1 {
	font-family: Poppins;
	font-size: 32pt;
	font-weight: lighter;
}
